<template>
  <div
    class="tree-wrapper"
    v-if="(data && data.length) || isOrganization"
    :style="{ height: `calc(100% - ${treeActionSheet ? '30px' : '0px'})` }"
  >
    <el-input
      size="medium"
      v-if="filterable"
      :placeholder="
        isOrganization ? '//' : ''
      "
      v-model="keyword"
    ></el-input>
    <div class="tree-body" :style="{ maxHeight: treeHeight }">
      <el-tree
        :data="data"
        ref="tree"
        :empty-text="emptyText"
        :props="options"
        :load="load"
        :lazy="lazy"
        :node-key="nodeKey"
        :highlight-current="highlightCurrent"
        :default-expand-all="defaultExpandAll"
        :show-checkbox="showCheckbox"
        :default-checked-keys="defaultCheckedKeys"
        :default-expanded-keys="defaultExpandedKeys"
        :filter-node-method="__filter"
        :expand-on-click-node="expandOnClickNode"
        :indent="indent"
        :current-node-key="currentNodeKey"
        :check-strictly="checkStrictly"
        :accordion="accordion"
        :allowDrag="__allowDrag"
        :allowDrop="__allowDrop"
        :draggable="draggable"
        @current-change="onCurrentChange"
        @node-drag-end="onNodeDropEnd"
        @node-contextmenu="onNodeContextMenu"
        @node-click="onNodeClick"
        @check-change="onCheckChange"
        @check="onCheck"
        @node-expand="onNodeExpand"
        @node-collapse="onNodeCollapse"
      >
        <tree-node
          slot-scope="{ node, data }"
          :disabled="node.data[options.disabled]"
          :options="options"
          v-model="node.data[options.label]"
          :isOrganization="isOrganization"
          :userCount="node.data[options.userCount]"
          :tag="node.data[options.tag]"
        ></tree-node>
      </el-tree>
    </div>
  </div>
</template>

<script>
import TreeNode from "./tree-node.vue";
export default {
  name: "jc-tree",
  components: {
    TreeNode,
  },
  props: {
    data: {
      // 
      type: Array,
      default() {
        return [];
      },
    },
    nodeKey: {
      type: String,
      default: "id",
    },
    treeHeight: {
      type: String,
      default: "calc(100% - 46px)",
    },
    treeActionSheet: {
      type: Boolean,
      default: false,
    },
    emptyText: {
      type: String,
      default: "",
    },
    props: {
      // 
      type: Object,
      default() {
        return {};
      },
    },
    load: Function, // 
    lazy: Boolean, // 
    highlightCurrent: {
      // 
      type: Boolean,
      default: true,
    },
    expandOnClickNode: Boolean,
    defaultExpandAll: Boolean, // 
    showCheckbox: Boolean, // 
    currentNodeKey: [String, Number], // key
    defaultCheckedKeys: {
      // key
      type: Array,
      default() {
        return [];
      },
    },
    defaultExpandedKeys: {
      type: Array,
      default() {
        return [];
      },
    },
    filterable: {
      // 
      type: Boolean,
      default: true,
    },
    filterNodeMethod: Function, // 
    accordion: Boolean, // 
    indent: {
      // 
      type: Number,
      default: 16,
    },
    draggable: {
      // 
      type: Boolean,
      default: true,
    },
    allowDrag: Function, //  Function (node) 
    allowDrop: Function, // 
    actionSheet: [],
    isOrganization: {
      type: Boolean,
      default: false,
    },
    checkStrictly: {
      //
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      keyword: "",
      tree: null,
      defaultProps: {
        label: "label",
        children: "children",
        disabled: "disabled",
        isLeaf: "isLeaf",
      },
      options: {},
    };
  },
  watch: {
    keyword(newVal, oldVal) {
      if (this.isOrganization && oldVal !== newVal) {
        this.$emit("select", newVal);
      } else if (this.$refs.tree && oldVal !== newVal) {
        this.$refs.tree.filter(this.keyword);
      }
    },
  },
  methods: {
    __initData(data) {
      if (!data) return;
      for (let i = 0; i < data.length; ++i) {
        this.$set(data[i], "__edit", false);
        if (data[this.options.children])
          this.__initData(data[this.options.children]);
      }
    },
    __filter(value, data, node) {
      if (
        this.filterNodeMethod &&
        typeof this.filterNodeMethod === "function"
      ) {
        return this.filterNodeMethod(value, data, node);
      } else {
        return this.__defaultFilterNodeMethod(value, data);
      }
    },
    __defaultFilterNodeMethod(value, data) {
      if (!value) return true;
      return this.props.label
        ? typeof this.props.label !== "function"
          ? data[this.props.label].indexOf(value) !== -1
          : data[this.props.label()].indexOf(value) !== -1
        : data.label.indexOf(value) !== -1;
    },
    __allowDrag(node) {
      if (this.allowDrag && typeof this.allowDrag === "function") {
        return this.allowDrag(node);
      } else return this.__defaultAllowDrag(node);
    },
    __defaultAllowDrag(node) {
      // 
      return node.level !== 1;
    },
    __allowDrop(draggingNode, dropNode, type) {
      if (this.allowDrop && typeof this.allowDrop === "function") {
        return this.allowDrop(draggingNode, dropNode, type);
      } else return this.__defaultAllowDrop(draggingNode, dropNode, type);
    },
    __defaultAllowDrop(draggingNode, dropNode, type) {
      // 
      return (
        draggingNode.level === dropNode.level &&
        draggingNode.parent === dropNode.parent &&
        type !== "inner"
      );
    },
    onCurrentChange(data, node) {
      // 
      let leaf =
        !data[this.options.children] || !data[this.options.children].length
          ? node
          : null;
      this.$emit("current-change", data, node, leaf);
    },
    onNodeDropEnd(prev, next, type, e) {
      if (next) {
        // next.parent.children
        this.$emit("node-drop-success", prev, next, type, e);
      } else {
        this.$emit("node-drop-failed", prev, type, e);
      }
    },
    onNodeClick(o, node, vm) {
      this.$emit("node-click", o, node, vm);
    },
    onNodeContextMenu(e, o, node, vm) {
      this.$emit("node-contextmenu", e, o, node, vm);
    },
    onCheckChange(o, checked, childrenChecked) {
      this.$emit("check-change", o, checked, childrenChecked);
    },
    onCheck(node, state) {
      this.$emit("check", node, state);
    },
    onNodeExpand(o, node, vm) {
      this.$emit("node-expand", o, node, vm);
    },
    onNodeCollapse(o, node, vm) {
      this.$emit("node-collapse", o, node, vm);
    },
    getCurrentNode() {
      // element ui bug???
      let nodeData = this.$refs.tree.getCurrentNode();
      if (nodeData) {
        return this.getNode(nodeData[this.nodeKey]);
      }
      return null;
    },
    getNode(key) {
      return this.$refs.tree.getNode(key);
    },
    setCurrentKey(nodeKey) {
      this.$refs.tree.setCurrentKey(nodeKey);
    },
    /**
     * @description data
     * @param {Object} node 
     * @param {Object} newNode 
     */
    append(node, newNode) {
      if (node && node.data && node.data.children && newNode) {
        node.data.children.push(newNode);
      } else if (node && node.data && newNode) {
        this.$set(node.data, "children", [newNode]);
      }
    },
    /**
     * @description 
     * @param {Object} node 
     */
    remove(node) {
      if (node) {
        const parent = node.parent;
        const data = node.data;
        const children = parent.data.children || parent.data;
        const index = children.findIndex(
          (d) => d[this.nodeKey] === data[this.nodeKey]
        );
        children.splice(index, 1);
      }
    },
    onClick(node) {
      node.data.__edit = true;
    },
    setChecked(key, checked, deep = false) {
      // 
      if (this.$refs.tree) this.$refs.tree.setChecked(key, checked, deep);
    },
    setCheckedKeys(keys, leafOnly) {
      if (this.$refs.tree) this.$refs.tree.setCheckedKeys(keys, leafOnly);
    },
    getCheckedNodes(leafOnly = false, includeHalfChecked = false) {
      return this.$refs.tree.getCheckedNodes(leafOnly, includeHalfChecked);
    },
  },
  mounted() {
    this.options = Object.assign({}, this.defaultProps, this.props);
    this.tree = this.$refs.tree;
  },
};
</script>

<style lang="scss">
.tree-wrapper {
  width: 100%;
  overflow: hidden;
  background: #fff;
  .el-input {
    margin-bottom: 10px;
  }
  .tree-body {
    // height: 100%;
    overflow: auto;
  }
  /**/
  div::-webkit-scrollbar {
    width: 9px;
    height: 10px;
    /**/
  }
  div::-webkit-scrollbar-track {
    background: #fff;
    border-radius: 2px;
  }
  div::-webkit-scrollbar-thumb {
    background: #bfbfbf;
    opacity: 0.8;
    border-radius: 10px;
  }
  div::-webkit-scrollbar-thumb:hover {
    background: rgb(59, 59, 59);
    opacity: 0.8;
  }
  div::-webkit-scrollbar-corner {
    background: #179a16;
  }
}
</style>
