export default {
  props: {
    selectApi: { // api
      type: String,
      required: true
    },
    params: { // 
      type: Object,
      default () {
        return {}
      }
    },
    autoSearch: { // 
      type: Boolean,
      default: true
    },
    showSummary: {
      type: Boolean,
      default: false
    },
    sumText: {
      type: String,
      default: ''
    },
    summaryMethod: {
      type: Function
    },
    emptyText: {
      type: String,
      default: ''
    },
    rowKey: {
      type: String
    },
    autoClearSelection: {
      type: Boolean
    }
  },
  data () {
    return {
      list: [],
      total: 0,
      pageNum: 1,
      pageSize: 25,
      loading: false,
      selection:[],
      query: {}
    }
  },
  methods: {
    select (reload, params = {}, api) {
      return this.__select(reload, params, api)
    },
    /**
         * @description 
         * @param [Boolean] reload 1
         * @param [Object] params 
         * @param [String] api api 
         * @author yangyu
         */
    async __select (reload, params = {}, api) {
      if (!api && !this.selectApi) return
      if (reload) this.pageNum = 1
      this.$nextTick(() => {
        let requestParams = Object.assign(this.params, params)
        requestParams['pageSize'] = this.pageSize
        requestParams['pageNum'] = this.pageNum
        this.loading = true
        return this.$api(api || this.selectApi, requestParams)().then(res => {
          if (res) {
            res.page.list.forEach(item => {
              if (item.supCode) {
                this.$set(item, 'visible', false)
              }
            })
            const selection = this.selection.map(e => e)
            if (this.autoClearSelection) this.clearTableSelection()
            this.list = res.page.list
            this.query = res.query
            this.pageNum = res.page.pageNum
            this.pageSize = res.page.pageSize
            this.total = res.page.total
            this.list.forEach(item=>{
              this.selection.forEach(select=>{
                if(item.code == select.code) this.$refs.table.toggleRowSelection(item, true)
              })
            })
            this.$nextTick(() => {
              if (this.$refs.table) {
                this.$refs.table.bodyWrapper.scrollTop = 0
              }
              if (this.autoClearSelection) {
                selection.forEach(e => {
                  const rowIndex = this.list.findIndex(row => row[this.rowKey] === e[this.rowKey])
                  if (rowIndex != -1) {
                    this.toggleTableRowSelection(this.list[rowIndex], true)
                  }
                })
              }
            })
          }
          this.loading = false
        }).catch(err => {
          this.alert(err, 'error')
          this.loading = false
        })
      })
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.select(true)
    },
    handleCurrentChange (val) {
      this.pageNum = val
      this.select()
    },
    /**
     * element-ui 
     * @author yangyu
     */
    /**
     * 
     * @param {*} currentRow
     * @param {*} oldCurrentRow
     */
    onTableCurrentChange (currentRow, oldCurrentRow) {
      this.$emit('table-current-change', currentRow, oldCurrentRow)
    },
    /**
     * 
     * @param {*} params
     */
    onTableSortChange (params) {
      this.$emit('table-sort-change', params)
    },
    /**
     * 
     * @param {*} selection
     */
    onTableSelectAll (selection) {
      this.selection = selection;
      this.$emit('table-select-all', selection)
    },
    /**
     * 
     * @param {*} selection
     */
    onTableSelectionChange (selection) {
      this.selection = selection;
      this.$emit('table-selection-change', selection)
    },
    /**
     * 
     * @param {*} filters
     */
    onTableFilterChange (filters) {
      this.$emit('table-filter-change', filters)
    },
    /**
     * 
     * @param {*} row
     * @param {*} event
     */
    onTableRowDbClick (row, event) {
      this.$emit('table-row-dbclick', row, event)
    },
    /**
     * 
     */
    clearTableSelection () {
      this.selection = []
      if (this.$refs.table) this.$refs.table.clearSelection()
    },
    /**
     * 
     * selected  true 
     * @param {*} row
     * @param {Boolean} selected
     */
    toggleTableRowSelection (row, selected) {
      this.selection = selected
      if (this.$refs.table) this.$refs.table.toggleRowSelection(row, selected)
    },
    /**
     * 
     */
    toggleTableAllSelection () {
      if (this.$refs.table) this.$refs.table.toggleAllSelection()
    },
    /**
     * 	expanded  true 
     * @param {*} row
     * @param {Boolean} expanded
     */
    toggleTableRowExpansion (row, expanded) {
      if (this.$refs.table) this.$refs.table.toggleRowExpansion(row, expanded)
    },
    /**
     * 
     * @param {*} row
     */
    setTableCurrentRow (row) {
      if (this.$refs.table) this.$refs.table.setCurrentRow(row)
    },
    /**
     * 
     */
    clearTableSort () {
      if (this.$refs.table) this.$refs.table.clearSort()
    },
    /**
     * 
     * columnKey
     * @param {*} columnKey
     */
    clearTableFilter (columnKey) {
      if (this.$refs.table) this.$refs.table.clearFilter(columnKey)
    },
    /**
     *  Table 
     * @param {String} prop 
     * @param {String} order 
     */
    tableSort (prop, order) {
      if (this.$refs.table) this.$refs.table.sort(prop, order)
    },
    /**
     *  Table  Table 
     */
    doTableLayout () {
      if (this.$refs.table) this.$refs.table.doLayout()
    }
  }
}
