<template>
  <div class="count-to-wrapper">
    <slot name="left" />
    <p class="content-outer">
      <span :class="['count-to-count-text', countClass]" :id="counterId">{{
        init
      }}</span
      ><i :class="['count-to-unit-text', unitClass]">{{ unitText }}</i>
    </p>
    <slot name="right" />
  </div>
</template>

<script>
import CountUp from "countup";
import "./index.less";
export default {
  name: "jc-count-up",
  props: {
    init: {
      type: Number,
      default: 0,
    },
    /**
     * @description 
     */
    startVal: {
      type: Number,
      default: 0,
    },
    /**
     * @description 
     */
    end: {
      type: Number,
      required: true,
    },
    /**
     * @description 
     */
    decimals: {
      type: Number,
      default: 0,
    },
    /**
     * @description 
     */
    decimal: {
      type: String,
      default: ".",
    },
    /**
     * @description 
     */
    duration: {
      type: Number,
      default: 2,
    },
    /**
     * @description 
     */
    delay: {
      type: Number,
      default: 0,
    },
    /**
     * @description easing
     */
    uneasing: {
      type: Boolean,
      default: false,
    },
    /**
     * @description 
     */
    usegroup: {
      type: Boolean,
      default: false,
    },
    /**
     * @description (usegroup)
     */
    separator: {
      type: String,
      default: ",",
    },
    /**
     * @description trueunit
     */
    simplify: {
      type: Boolean,
      default: false,
    },
    /**
     * @description [3, 'K+'], [6, 'M+']36k+
     *              10001K+
     */
    unit: {
      type: Array,
      default() {
        return [
          [3, "K+"],
          [6, "M+"],
          [9, "B+"],
        ];
      },
    },
    countClass: {
      type: String,
      default: "",
    },
    unitClass: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      counter: null,
      unitText: "",
    };
  },
  computed: {
    counterId() {
      return `count_to_${this._uid}`;
    },
  },
  methods: {
    getHandleVal(val, len) {
      return {
        endVal: parseInt(val / Math.pow(10, this.unit[len - 1][0])),
        unitText: this.unit[len - 1][1],
      };
    },
    transformValue(val) {
      let len = this.unit.length;
      let res = {
        endVal: 0,
        unitText: "",
      };
      if (val < Math.pow(10, this.unit[0][0])) res.endVal = val;
      else {
        for (let i = 1; i < len; i++) {
          if (
            val >= Math.pow(10, this.unit[i - 1][0]) &&
            val < Math.pow(10, this.unit[i][0])
          )
            res = this.getHandleVal(val, i);
        }
      }
      if (val > Math.pow(10, this.unit[len - 1][0]))
        res = this.getHandleVal(val, len);
      return res;
    },
    getValue(val) {
      let res = 0;
      if (this.simplify) {
        let { endVal, unitText } = this.transformValue(val);
        this.unitText = unitText;
        res = endVal;
      } else {
        res = val;
      }
      return res;
    },
  },
  mounted() {
    this.$nextTick(() => {
      let endVal = this.getValue(this.end);
      this.counter = new CountUp(
        this.counterId,
        this.startVal,
        endVal,
        this.decimals,
        this.duration,
        {
          useEasing: !this.uneasing,
          useGrouping: this.useGroup,
          separator: this.separator,
          decimal: this.decimal,
        }
      );
      setTimeout(() => {
        if (!this.counter.error) this.counter.start();
      }, this.delay);
    });
  },
  watch: {
    end(newVal) {
      let endVal = this.getValue(newVal);
      this.counter.update(endVal);
    },
  },
};
</script>
